@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@600&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-size: 62.5%;
  font-family: 'Montserrat';
}
#root {
  position: relative;
  height: 100vh;
}
ul {
  list-style: none;
}
::-webkit-scrollbar {
  display: none;
}