@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@600&display=swap);
.menu{position:absolute;z-index:100;font-size:2rem;display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;height:100%;left:-100vw}.menu--side-menu{position:relative;background-color:#b4b4b4;width:100vw;left:0;border-right:solid 15px white;transition:0.5s}.menu--side-menu li{height:10vh;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;padding-left:5%;border-bottom:solid 2px rgba(245,245,245,0.8);background-color:#0f4bf0;color:white}.btn-options{position:absolute;z-index:100;font-size:2rem;height:45px;margin:15px}.open-sidemenu{left:100vw}.hide{display:none}

.display{position:absolute;width:100%;bottom:70vh;z-index:1;display:-webkit-flex;display:flex;background-color:#f5f5f5;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:flex-end;justify-content:flex-end;transition:0.5s}.display .history-block{width:100%;height:70vh;border-bottom:solid 2px #b4b4b4;overflow:scroll;display:block}.display .line{width:100%;height:2px;margin:0 auto 0 auto;background-color:#b4b4b4;opacity:0.5}.display--box{height:15vh;width:100%;display:-webkit-flex;display:flex;-webkit-justify-content:flex-end;justify-content:flex-end;-webkit-align-items:center;align-items:center;padding:0 6%}.display--box span{color:#6e6e6e;font-size:2.2rem}.display .result span{color:#0f4bf0;font-size:1.8rem}.display .btn-history{position:absolute;bottom:0;height:30px;width:100%;display:-webkit-flex;display:flex}.display .btn-history div{background-color:#6e6e6e;height:8px;width:30px;border-radius:4px;margin:auto}.history-swipe{bottom:0}

.keys{display:grid;grid-template-columns:repeat(4, 1fr);grid-template-rows:repeat(5, 1fr);position:absolute;bottom:0;width:100%;height:70vh}.keys .btn{position:relative;font-size:2rem;width:100%;height:100%;background-color:#ff4b1e;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;border:1px solid #b4b4b4}.keys .number{background-color:#b4b4b4}.keys .operator{background-color:#8a8a8a}.keys .btn:hover{opacity:0.9}

*{margin:0;padding:0;box-sizing:border-box}body{font-size:62.5%;font-family:'Montserrat'}#root{position:relative;height:100vh}ul{list-style:none}::-webkit-scrollbar{display:none}

